.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  top: 50%;
  left: 50%;

  background: #0c4a6e;
  color: white;

  width: 80%;
  height: auto;

  font-size: 4vh;

  transform: translate(-50%, -50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
